.work-experience-parent-div {
    width: 65vw;
    height: 60vh;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    overflow-y: hidden;
}

.work-experience-child-div {
    width: 100%;
    height: 100%;
    margin: 0;
    overflow-x: auto;
    overflow-y: auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
}

.backend-div {
    padding-left: 1rem;
}

.frontend-div {
    padding-left: 1rem;
}

.work-experience-heading {
    padding: 0;
    margin: 0;

    .company-name {
        margin: 0;
        font-size: 32px;
        font-weight: 700;

        .position {
            font-family: cursive;

            font-size: 30px;
            font-weight: 900;
        }
    }


    .tenure {
        margin: 0;
        font-size: 22px;
        font-weight: 700;
    }
}




.work-experience-description {
    .role-heading {
        margin: 0;
        font-size: 18px;
        font-weight: 700;
    }

    .role-tasks {
        margin: 0;
        margin-left: 1rem;
        margin-bottom: 10px;
        font-size: 16px;
    }

    .work-experience-list-style {
        list-style: circle;
    }

}